/* stylelint-disable */
*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
}

body {
	margin: 0;
	font-family: "Montserrat", "Arial", sans-serif;
	font-size: 16px;
	font-weight: 400;
	line-height: 1.6;
	color: $textColor;
	background-color: #ffecb8;
}

ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

.container {
	min-width: 320px;
	max-width: 1920px;
	margin: 0 auto;
}

/* =====logo========= */
.st00 {
	fill: #bd171d
}

.st11 {
	fill: #673a27
}

/*=============*/
.glob__btn {
	background-color: transparent;
	border: 0;
	padding: 0;
	cursor: pointer;
}

.glob__btn-wrap {
	position: relative;
	display: flex;
	min-width: 100px;
	min-height: 40px;
	padding: 10px 3vmin;
	z-index: 0;
}

.glob__btn-bg {
	position: absolute;
	top: 0;
	left: 0;
	color: $captionColor;
	filter: drop-shadow(3px 2px 2px rgba(0, 0, 0, 0.54));
	width: 100%;
	height: 100%;
	z-index: -1;
}

.glob-h2 {
	margin: 0 0 40px 0;
	font-family: "AdelleCyrillic-Extrabold", "Georgia", serif;
	font-size: $font14;
	letter-spacing: 1.3px;
	text-align: center;
	color: $titleColor;
	text-transform: uppercase;
}

.glob-h2-red {
	font-family: "AdelleCyrillic-Extrabold", "Georgia", serif;
	font-size: $font24;
	color: #ba0b11;
	text-transform: uppercase;
}

.to_top {
	display: none;
	position: fixed;
	bottom: 30px;
	right: 10px;
	width: 40px;
	height: 40px;
	text-align: center;
	line-height: 38px;
	box-shadow: 0 0 5px 1px rgba(103, 58, 39, 0.8);
	font-size: 22px;
	color: #ba0b11;
	background-color: #ffecb8;
	cursor: pointer;
	border-radius: 5px;
	z-index: 999;
}

.to_top:active {
	color: #000000;
}

.to_top-show {
	display: block;
}

@media (min-width: $mobileMini) {
	.glob-h2-red {
		font-size: $font26;
		color: #ba0b11;
	}
}

@media (min-width: $mobile) {
	.glob__btn-wrap {
		position: relative;
		display: flex;
		min-width: 100px;
		min-height: 40px;
		padding: 10px 30px;
		z-index: 0;
	}
}

@media (min-width: $mobileWide) {
	.glob-h2 {
		font-size: $font18;
	}

	.glob-h2-red {
		font-size: $font30;
	}

	.to_top {
		right: 20px;
	}
}

@media (min-width: $tablet) {
	.glob-h2 {
		font-size: $font22;
	}

	.glob-h2-red {
		font-size: $font34;
	}

	.glob__btn-wrap {
		padding: 17px 30px;
	}

	.to_top {
		right: 30px;
	}
}

@media (min-width: $medium) {
	.to_top {
		width: 50px;
		height: 50px;
		font-size: 24px;
		line-height: 50px;
	}
}

@media (min-width: 1600px) {
	.glob-h2 {
		font-size: $font28;
	}

	.glob-h2-red {
		font-size: $font44;
	}
}

.r {
	outline: 1px dotted red;
}

.b {
	outline: 1px dotted blue;
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}
